import React from 'react';
// import jQuery from 'jquery';

import {GetImageSize} from './common';

export default class BackImageComponent extends React.Component {
	constructor(props) {
		super(props);
		const {cWidth, cHeight, cLeft, cTop} = GetImageSize(window.innerWidth, window.innerHeight);
		this.state = {cWidth, cHeight, cLeft, cTop, imgBlur:true, circlePos:'', dX:0, dY:0};
	}

	componentDidMount() {
		// jQuery('.back-image').css({backgroundImage:"url('./texture/NAV ARRAY_02247.jpg')"});
		window.addEventListener('resize', (e) => {
			const {cWidth, cHeight, cLeft, cTop} = GetImageSize(window.innerWidth, window.innerHeight);
			this.setState({cWidth, cHeight, cLeft, cTop})
		})
	}

	UNSAFE_componentWillReceiveProps(nextProps) {
		if (!this.state.transEnd && nextProps.transEnd) {

			this.setState({transEnd:true}, () => {
				const sTime = 1000, aTime = 1000;
				setTimeout(() => { this.setState({circlePos:'left'}) }, sTime);
				setTimeout(() => { this.setState({circlePos:''}) }, sTime+aTime);
				setTimeout(() => { this.setState({circlePos:'right'}) }, sTime+aTime * 2);
				setTimeout(() => { this.setState({circlePos:''}) }, sTime+aTime * 3);
				setTimeout(() => { this.setState({imgBlur:false}) }, sTime+aTime * 4);
				setTimeout(() => { this.props.endImageBlur() }, sTime+aTime * 4 + 1000);
			});
		}
	}

	render() {
		const {cWidth, cHeight, cLeft, cTop, imgBlur, circlePos} = this.state;
		return (
			<div className={`back-board back-image active`}>
				<img id='canvasBack' className={`${imgBlur?'blur':''}`} src={this.props.imgArr[this.props.selImgNum].src} style={{width:cWidth+'px', height:cHeight+'px', left:cLeft+'px', top:cTop+'px'}} alt="" />
				{imgBlur &&
					<>
						<div className={`drag-circle ${circlePos}`}></div>
						<div className='drag-label'>DRAG TO EXPLORE</div>
					</>
				}
			</div>
		);
	}
}
