import React from 'react';
import imgLogo from '../assets/images/logo.png';
import imgCross from '../assets/images/cross.png';
import imgComo from '../assets/images/como.png';

export default class LogoComponent extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	componentDidMount() {
		setTimeout(() => {
			this.setState({logoShow:true});
		}, 1000);
	}

	UNSAFE_componentWillReceiveProps(nextProps) {
	}

	onClickLogo = () => {
		window.open('https://comofootball.com/', '_blank').focus();
	}

	render() {
		const {logoShow} = this.state;
		return (
			<div className={`back-board logo-wrapper active ${logoShow?'show':''}`}>
				<div className='img-logo'>
					<img src={imgLogo} onClick={this.onClickLogo} alt="" />
				</div>
				<div className='img-cross'>
					<img src={imgCross} alt="" />
				</div>
				<div className='img-como'>
					<img src={imgComo} alt="" />
				</div>
			</div>
		);
	}
}
