import React from 'react';
import imgPattern from '../assets/images/pattern.jpg';

export default class TransComponent extends React.Component {
	constructor(props) {
		super(props);
		this.state = {transShow:props.transShow};
	}

	componentDidMount() {
	}

	UNSAFE_componentWillReceiveProps(nextProps) {
		if (this.state.transShow !== nextProps.transShow) {
			this.setState({transShow:nextProps.transShow})
		}
	}

	render() {
		const {transShow} = this.state;
		return (
			<div className={`back-board trans-page ${transShow?'active':''}`}>
				<div className='trans-back trans-white'></div>
				<img className={`trans-back trans-image ${transShow?'active':''}`} src={imgPattern} alt="" />
				<div className={`trans-back trans-blue ${transShow?'active':''}`}></div>
			</div>
		);
	}
}
