import React from 'react';
import jQuery from 'jquery';
import LoadingComponent from './Loading';
import BackImageComponent from './BackImage';
import CanvasComponent from './Canvas';
import LogoComponent from './Logo';
import TransComponent from './Trans';
import VideoOnboarding from '../assets/video/onboarding.mp4';
import {exSize} from './common';
import imgVideoFrame from '../assets/images/video-first.jpg';

import '../assets/css/index.css';

const imgFirstNum=1, imgLastNum=969, imgIntNum = 6, ratioMouseImage = 3;
const transEnd = 43, transFullNum = 18, transStart = 11;
export default class MainComponent extends React.Component {
	constructor(props) {
		super(props);
		const device = ( window.innerHeight > window.innerWidth || window.innerWidth < 1280 ) ? 'mobile':'web'; //  || window.innerWidth < 1360
		this.downX = null; this.dX = 0; this.dY = 0;
		this.state = {pageKey:'', device, loadCheck:false, loading:true, transFrame:-1};// , videoHide:true
	}

	componentDidMount() {
		this.loadImageArr();
		window.addEventListener('mousedown', this.onMouseDown, false);
		window.addEventListener('mousemove', this.onMouseMove, false);
		window.addEventListener('mouseup', this.onMouseUp, false);
	}

	onMouseDown = (e) => {
		this.downX=e.pageX;
		this.oldImgNum = this.state.selImgNum;
		this.moveX = e.pageX;
		this.setState({pageX:e.pageX});
		this.mouseStatus = 'down';
	};
	onMouseUp = () => { this.mouseStatus = null};
	onMouseMove = (e) => {
		const halfX = window.innerWidth/2, halfY = window.innerHeight/2;
		this.dX = Math.round((halfX - e.pageX)/halfX * exSize * 0.9);
		this.dY = Math.round((halfY - e.pageY)/halfY * exSize * 0.9);

		if (!this.mouseStatus || !this.imgArr) return;
		this.moveX = e.pageX;
		}

	onClickHotspot = (item) => {
		this.setState({selPos:null, selHotspot:item});
	}

	loadImageArr = () => {
		const imgUrlArr = [];
		for (let i = imgFirstNum; i < imgLastNum; i+=imgIntNum) {
			imgUrlArr.push('./assets/images/array ('+i.toString()+').jpg');
		}
		const selMapArr = imgUrlArr.map(imgUrl => this.loadImage(imgUrl));

		Promise.all(selMapArr).then((imgArr) => {
			this.imgArr = imgArr;
			this.setState({loading:false, selImgNum:Math.round(imgArr.length/2)});
			var onboardingVideo = document.getElementById('onboardingVideo');
			onboardingVideo.play();
			setTimeout(() => { this.setState({transShow:true}); this.animate(); }, transStart * 1000);
			setTimeout(() => { this.setState({videoHide:true}); }, transStart * 1000 + 1000);
			setTimeout(() => { this.setState({transEnd:true}); }, transStart * 1000 + 3000);
		}).catch((e) => { console.log(e); });
	}

	loadImage = (url) => {
		return new Promise((resolve, reject) => {
			let img = new Image();
			img.addEventListener('load', e => resolve(img));
			img.addEventListener('error', () => {
				reject(new Error(`Failed to load image's URL: ${url}`));
			});
			img.src = url;
		});
	}

	setTransFrame = (num) => {
		this.setState({transFrame:num}, () => {
			if (num>=transEnd) return;
			if (num === transFullNum) this.setState({videoHide:true});
			setTimeout(() => {
				this.setTransFrame(num+1);
			}, 50);
		})
	}

	animate = () => {
		const element = jQuery('#canvasBack');
		if (element) {
			const transVal = element.css("-webkit-transform") ||
							element.css("-moz-transform")    ||
							element.css("-ms-transform")     ||
							element.css("-o-transform")      ||
							element.css("transform");
			if (transVal) {
				const matrix = transVal.replace(/[^0-9\-.,]/g, '').split(',');
				var tX = matrix[12] || matrix[4] || 0;
				var tY = matrix[13] || matrix[5] || 0;
				tX = parseFloat(tX); tY = parseFloat(tY);
				tX += ( this.dX - tX ) * .05;
				tY += ( this.dY - tY ) * .05;
				jQuery("#render2d").css( "transform", 'translate('+tX+'px, '+tY+'px)' );
				jQuery("#canvasBack").css( "transform", 'translate('+tX+'px, '+tY+'px)' );
			}
		}

		var {pageX, selImgNum} = this.state;
		if (pageX && selImgNum && this.moveX) {
			pageX += ( this.moveX - pageX ) * .2;
			const deltaImgNum = Math.round((pageX - this.downX)/ratioMouseImage);
			const newImgNum = this.oldImgNum + deltaImgNum;
			if (newImgNum < 10 || newImgNum >= this.imgArr.length - 10) {
	
			} else {
				selImgNum = newImgNum;
			}
			const imgSrc = this.imgArr[selImgNum].src;
			setTimeout(() => {
				jQuery("#canvasBack").attr("src", imgSrc);
				this.setState({pageX, selImgNum})
			}, 0);
		}

		requestAnimationFrame(this.animate);
	};

	render() {
		const {pageKey, device, loadCheck, loading, selImgNum, videoHide, endBlur, transShow, transEnd} = this.state;
		return (
			<div className={`page-wrapper ${device} ${pageKey}-page`}>
				<div className={`back-board back-image`}>
					<img src={imgVideoFrame} style={{minWidth:'100vw', minHeight:'100vh', right:'0', bottom:'0', filter: 'blur(8px)'}} alt="" />
				</div>
				{loading === false &&
					<>
						<BackImageComponent
							imgArr={this.imgArr}
							selImgNum={selImgNum}
							transEnd={transEnd}
							endImageBlur={()=>this.setState({endBlur:true})}
							initImgNum={Math.round((imgFirstNum+imgLastNum)/2)}
						></BackImageComponent>
						<CanvasComponent
							imgCount={this.imgArr.length}
							selImgNum={selImgNum}
							endBlur={endBlur}
							onClickHotspot={this.onClickHotspot}
						></CanvasComponent>
					</>
				}
				<video id='onboardingVideo' className={`onboarding-video ${videoHide?'hide':''}`} muted style={{filter: `blur(${loading?'8':'0'}px)`}}>
					<source type="video/mp4" src={VideoOnboarding}></source>
				</video>
				<LoadingComponent
					loadCheck={loadCheck}
					loading={loading}
					pageKey={pageKey}
					setLoadCheck={()=>this.setState({loadCheck:true})}
				></LoadingComponent>
				<TransComponent
					transShow={transShow}
				></TransComponent>
				<LogoComponent></LogoComponent>
			</div>
		);
	}
}
