import React from 'react';

export default class LoadingComponent extends React.Component {
	constructor(props) {
		super(props);
		this.state = {loading: props.loading, pageKey:props.pageKey, device:props.device, loadCheck:props.loadCheck};
	}

	componentDidMount() {
	}

	UNSAFE_componentWillReceiveProps(nextProps) {
		if (this.state.pageKey !== nextProps.pageKey) {
			this.setState({pageKey:nextProps.pageKey});
		}
		if (this.state.loading !== nextProps.loading) {
			this.setState({loading:nextProps.loading});
		}
	}

	render() {
		const {loading} = this.state;
		return (
			<div className={`back-board loading-wrapper ${loading?'active':''}`}>
				<div className='loading-circle' id='loading0'></div>
				<div className='loading-circle' id='loading1'></div>
				<div className='loading-circle' id='loading2'></div>
			</div>
		);
	}
}
