import React from "react";
// import jQuery from "jquery";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { CSS2DRenderer, CSS2DObject } from "three/examples/jsm/renderers/CSS2DRenderer.js";
import { hotInfoArr } from "./common";

export default class CanvasComponent extends React.Component {
	constructor(props) {
		super(props);
		this.backMapArr = [];
		this.preMapArr = [];
		this.middleFrame = this.props.imgCount / 2;
		this.deltaRot = -1 / this.middleFrame;
		this.state = { device: props.device, selImgNum: props.selImgNum };
	}

	componentDidMount() {
		this.setCanvasSize();
		this.initScene();
		this.animate();
	}

	UNSAFE_componentWillReceiveProps(nextProps) {
		if (!this.state.endBlur && nextProps.endBlur) {
			this.setState({ endBlur: true }, () => {
				this.addHotSpot();
			});
		}
		if (this.state.selImgNum !== nextProps.selImgNum) {
			this.setState({ selImgNum: nextProps.selImgNum }, () => {
				const deltaFrame = this.middleFrame - this.state.selImgNum;
				this.totalGroup.rotation.y = deltaFrame * this.deltaRot;
			});
		}
	}

	addHotSpot = () => {
		// const testGeo = new THREE.BoxGeometry(8, 0.1, 4);
		// const testMat = new THREE.MeshStandardMaterial({ color: 0x000000, transparent: true, opacity: 0.01 });
		// const testMesh = new THREE.Mesh(testGeo, testMat);
		const hotSize = 0.1;
		const hotGeo = new THREE.BoxGeometry(hotSize, hotSize, hotSize);
		const hotMat = new THREE.MeshStandardMaterial({ color: 0xff0000, transparent: true, opacity: 0.001, });
		const hotMesh = new THREE.Mesh(hotGeo, hotMat);
		hotInfoArr.forEach((hotItem) => {
			const cloneHot = hotMesh.clone();
			cloneHot.hotKey = hotItem.key;
			cloneHot.hotLabel = hotItem.label;
			cloneHot.position.set(hotItem.x, hotItem.y, hotItem.z);
			console.log(cloneHot);
			this.totalGroup.add(cloneHot);
		});

		this.totalGroup.children.forEach((hotMesh) => {
			if (!hotMesh.hotKey || !hotMesh.hotLabel) return;
			const hotDiv = document.createElement("div");
			// hotDiv.id = 'hotWrapper_'+hotItem.hotId;
			hotDiv.classList.add("hot-item", "none");
			hotDiv.innerHTML =
				"<div class='hot-circle'></div><div class='hot-label' onclick='ClickHotSpot(\""+hotMesh.hotKey+"\")'>" + hotMesh.hotLabel + "</div>";

			const hotLabel = new CSS2DObject(hotDiv);
			hotLabel.position.set(0, 0, 0);
			hotMesh.add(hotLabel);
		});
	};

	// setRender2D = () => {
	// 	jQuery("#render2d").css(
	// 		"display",
	// 		this.state.pageKey === "room" && !this.state.expand ? "block" : "none"
	// 	);
	// };

	initScene = () => {
		this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
		this.renderer.setSize(this.cWidth, this.cHeight);
		if (!document.getElementById("container")) return false;
		document.getElementById("container").appendChild(this.renderer.domElement);
		this.renderer.setClearColor(0xd8d8d8, 0);

		this.camera = new THREE.PerspectiveCamera(
			45,
			this.cWidth / this.cHeight,
			0.01,
			1000
		);
		this.camera.position.set(0, 6, 9);

		this.scene = new THREE.Scene();
		this.totalGroup = new THREE.Group();
		this.scene.add(this.totalGroup);

		this.render2d = new CSS2DRenderer();
		this.render2d.setSize(window.innerWidth, window.innerHeight);
		this.render2d.domElement.id = "render2d";
		this.render2d.domElement.classList.add(this.props.device);
		document.body.appendChild(this.render2d.domElement);
		const render2d = document.getElementById("render2d");
		render2d.classList.add(this.props.device);

		this.controls = new OrbitControls(this.camera, this.renderer.domElement);
		this.controls.enabled = false;
		// this.controls.enablePan = false;
		// this.controls.enableRotate = false;

		const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
		this.scene.add(ambientLight);
		const dirLight = new THREE.DirectionalLight(0xffffff, 0.5);
		this.scene.add(dirLight);
		dirLight.position.set(1, 1, 1);
	};

	setCanvasSize = () => {
		this.cWidth = window.innerWidth;
		this.cHeight = window.innerHeight;
		if (this.renderer && this.camera) {
			this.renderer.setSize(this.cWidth, this.cHeight);
			this.render2d.setSize(this.cWidth, this.cHeight);
			this.camera.aspect = this.cWidth / this.cHeight;
			this.camera.updateProjectionMatrix();
		}
	};

	animate = () => {
		if (!this.camera || !this.scene) return;
		requestAnimationFrame(this.animate);
		this.camera.lookAt(0, 0, 0);
		this.renderer.render(this.scene, this.camera);
		this.render2d.render(this.scene, this.camera);
	};

	render() {
		// const { pageKey } = this.state;
		return (
			<div className={`back-board page-back canvas active`}>
				<div id="container"></div>
			</div>
		);
	}
}
