import { autoPlay } from "es6-tween";

autoPlay(true);

export const exSize = 40;

export function GetImageSize (backW, backH) {
	backW += exSize; backH += exSize;
	const imgRatio = 16/9, backR = backW/backH;
	var cWidth, cHeight, cLeft, cTop;
	if (backR > imgRatio) {
		cWidth = backW; cHeight = Math.round(cWidth / imgRatio); cTop = (backH - cHeight) / 2; cLeft = exSize/-2;
	} else {
		cHeight = backH; cWidth = Math.round(cHeight * imgRatio); cLeft = (backW - cWidth) / 2; cTop = exSize/-2;
	}
	return {cWidth, cHeight, cLeft, cTop};
}

